	module.exports = {"data":{"markdownRemark":{"html":"<p>クラス。学生にとって、本当に良い先生は、自分の時間のほとんどを学生に費やす必要があります。高等教師のようなものはありません。教師だけが高いレベルを与えることができ、その後彼は有名な教師です。生徒を教育することは、教育者の職業です。この精神がなければ、いい生徒はいないし、学校はそれをうまくやることができない。しかし、現在の社会環境や個人の生活状況によって、人々は学生の教育に集中することができません。先生はあまりにも邪魔され、誘惑が余りにもあります。復旦の近くのいくつかの証券取引所を見ると、いつも復旦人がいます。まず第一に、政策が必要です。第二に、精神を持つことが重要です。先生は一種の精神ですが、この種の精神はどうして孤独になるのですか？静かで遠くの明治に無関心。それは平和と無限の機会を高めることです。簡単に言うことはできません。</p>\n<p>「211プロジェクト」復旦をどう発展させるか、新たな成長ポイントを見つけてメカニズムを合理化する方法の問題について話し合ってみましょう。小規模な大学の組織を形成するために、人が多すぎる場合、部長の主なエネルギーは人事関係と人的衝突に費やされます。より多くの人々がいる場合、散逸構造を形成することは容易であり、この「ブラックホール」で電力が消費される。中国文化の特徴はおそらくこのようなものです。実際、外国は想像以上に単純ではなく同じです。この問題を解決するために、大学の小規模部門だけ、部長の主なエネルギーは、教育、研究、管理を把握することです。複雑な人事関係を簡単にする。人事関係の簡素化は、組織の構成に依存することがあります。人事関係は単純でシンプルでシンプルでなければなりません。これにより生産性が向上します。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0061.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0061.md","frontmatter":{"title":"3月17日 木曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0061.md absPath of file >>> MarkdownRemark"}}

